<template>
    <div class="a2-page">
        <div style="height: 100%;" :class="['flex-column', {'load': sscHistoryList == false}]">
            <div class="prize-list flex-column-grow">
                <scroller :on-refresh="refresh" :on-infinite="infinite" ref="myscroller" noDataText="今日还没有历史开奖哦～">
                    <B-scroller>
                        <ul>
                            <template v-for="item in orderList">
                                <li :key="item.number + Math.random()">
                                    <div class="a">
                                        <div class="pict">
                                            <img :src="require(`@/static/img/play/${item.playGroupId}.png`)" alt="">
                                        </div>
                                        <div class="fl-layout two">
                                            <h2 class="white">{{params.name}}<p>{{item.number}}期 　{{item.openTime | timer}}</p></h2>
                                            <div class="number" v-if="item.openCode">
                                                <div class="number-component" v-if="playHandle(item.playGroupId)">
                                                    <component :image="true" v-bind:is="'OpenCode-' + params.id" :number="item.openCode.split(',')"></component>
                                                </div>
                                                <div v-else>
                                                    <span v-for="(item1, index) in item.openCode.split(',')" :key="index">{{item1}}</span>
                                                </div>
                                            </div>
                                            <div class="number" v-else>
                                                <p>正在开奖...</p>
                                            </div>
                                        </div>
                                    </div>
                                </li>
                            </template>
                        </ul>
                    </B-scroller>
                </scroller>
            </div>
        </div>
    </div>
</template>

<script>
import { getToday1 } from "@/tool/common/time.js";
import scroller from '@/tool/plug/mixins/scroller.js'

export default {
    mixins:[ scroller ],
    data(){
        return {
            sscHistoryList: false,
            params: {
                id: '',
                name:'',
            },
            tabs: 0,

            data_me: 'getNetData',
        }
    },
    created(){
        this.params.id = this.baseUser.playGroupId
        console.log(this.$data);
        this.getSscPlayGroup();
    },
    watch:{
        tabs(){
            this.sscHistoryList = false
            this.goodsAll(true);
        }
    },
    methods: {
        // 彩种详情
        getSscPlayGroup(){
            var data = {
                playGroupId: this.baseUser.playGroupId,
            }
            this.$store.dispatch('Game/getSscPlayGroup', data)
            .then(res=>{
                console.log(res);
                this.params.name = res.name
            })
        },
        playHandle(id){
            let arr = [14, 15, 23, 27, 29, 20, 9, 22, 30, 31, 32, 6, 18, 19]
            return arr.contains(id);
        },
        getNetData(done){
            
            let data = {
                startTime: '',
                endTime: '',
                pageSize: 20,
                pageIndex: this.page,
                openDate:'',
                number:'',
                playGroupId: this.params.id,
            }

            switch (this.tabs) {
                case 0:
                    var t = getToday1();
                    data.startTime = t.start
                    data.endTime = t.end
                    break;
                case 1:
                    var t = lastWeek();
                    data.startTime = t.fristWday
                    data.endTime = t.lastWday
                    break;
                case 2:
                    var t = thisMonth();
                    data.startTime = t.thisMonthStart
                    data.endTime = t.thisMonthEnd
                    break;
                default:
                    break;
            }
            
            this.$api.game.getDataHistory(data)
            .then(res=>{
                console.log(res);
                this.sscHistoryList = res;
                this.curr_success(done, res.sscHistoryList, res.pageNum, res.lastPage)
            })    
        },
    }
}
</script>

<style lang="stylus" scoped>
    .a2-page
        position absolute
        left 0
        right 0
        bottom 0
        top 0
    .prize-list-tabs
        span
            color #fff
</style>