<template>
    <div :class="['jump-wrapper-one', {'load': pageData == false || null_data}]">
        <scroller class="flex-column-grow" :on-refresh="refresh" :on-infinite="infinite" ref="myscroller" noDataText="今日没有更多记录～">
            <B-scroller>
                <ul>
                    <li v-for="item in orderList" :key="item.pk">
                        <div class="pict">
                            <img :src="require(`@/static/img/play/${item.playGroupId}.png`)" alt="">
                        </div>
                        <div class="text">
                            <div class="title">
                                <h2>{{item.content}}</h2>
                                <div v-html="resultHandle(item)"></div>                            
                            </div>
                            <p>期号：{{item.number}}</p>
                        </div>
                        <div class="fr-box">
                            <h2>
                                <span :class="item.winOrLose > 0 ? 'a1':'a2'">{{item.winOrLose}}</span>
                                <var>元</var>
                            </h2>
                            <p>{{item.createTime | timer}}</p>
                        </div>
                    </li>
                </ul>
            </B-scroller>
        </scroller>
        <div class="xuanfu" v-if="pageData">当前小计{{pageData.pageMoney}}元，总计{{pageData.totalMoney}}元，当前盈亏{{pageData.pageWinOrLoseMoney}}元，总盈亏{{pageData.totalWinOrLoseMoney}}元</div>
    </div>
</template>

<script>
    import { getToday1 } from "@/tool/common/time.js";
    import scroller from '@/tool/plug/mixins/scroller.js'
    export default {
        mixins:[ scroller ],
        data(){
            return {
                null_data: false,
                pageData: false,
                data_me: 'getNetData'
            }
        },
        created(){
            console.log(this.$data);
            const page1 = this.$assist.getLocalStorage('page1');
            const user = this.$assist.getLocalStorage('user');
            if(page1 && this.baseUser.uid === user){
                this.pageData = page1;
                this.orderList = page1.sscBetsList;
                this[this.data_me](()=>{
                    console.log('ok');
                });
            }
        },
        methods:{
            resultHandle(item){
                if(item.status == 0){
                    return `<span class="b1">待结算</span>`
                }else if (item.status == 1){
                    if(item.winOrLose > 0){
                         return `<span class="b2">已中奖</span>`
                    }else{
                         return `<span class="b3">未中奖</span>`
                    }
                }else{
                    return `<span class="b4">已撤单</span>`
                }
            },
            getNetData(done){
                let data = {
                    pageIndex: this.page,
                    pageSize: 20,
                    startTime: getToday1().start,
                    endTime: getToday1().end,
                    status:'',
                    isZhongjiang:'',
                    ...this.baseUser,
                    playGroupId:'',

                }
                this.$api.user.getSscBetsList(data)
                .then(res=>{
                    this.pageData = res;
                    if(res.result == 1){
                        if(res.sscBetsList.length == 0){
                            this.null_data = true;
                        }
                        if(this.page == 1){
                            this.$assist.setLocalStorage('page1', res)
                            this.$assist.setLocalStorage('user', this.baseUser.uid)
                        }
                        this.curr_success(done, res.sscBetsList, res.pageNum, res.lastPage)
                    // this.mui_scroll('.jump-wrapper-one');
                    }else{
                        this.$refs.myscroller.finishInfinite(true);
                    }
                })
            }
        },

    }
</script>

<style scoped lang="stylus">
    ._v-container
        height: calc(100% - .9rem) !important;
    .xuanfu
        height .9rem
        display flex
        align-items center
        position absolute
        left 0
        right 0
        bottom 0
        line-height 1.4
        background #ccc
        z-index 2
        padding 0 .15rem
</style>