
export default {
    data(){
        return {
            orderList:[],
            page: 0,
            all_page: 1,
        }
    },
    methods: {
        // 下拉刷新
        refresh (done) {
            setTimeout(()=>{
                this.goodsAll();
                this.page = 1;
                this[this.data_me](()=>{
                    console.log('ok');
                });
                done();
            },1500)
        },
        //上拉加载更多
        infinite (done) {
            console.log('infinite');
            setTimeout(()=>{
                if(this.page >= this.all_page){
                    this.$refs.myscroller.finishInfinite(true);
                }else{
                    this.page++;
                    this[this.data_me](done);
                }
            }, 500)
        },
        // 如果涉及到tab栏切换，需要重新设置scroller的状态.finishInfinite(false)参数为false时会从新调用一次上拉函数，从而重置当前scroller的状态
        goodsAll(){
            this.page = 0;
            this.all_page = 1;
            this.$refs.myscroller.finishInfinite(false);
            // this.orderList = [];
        },
        curr_success(done, data, curr_page, all_page){
            if (curr_page == 1){
                this.orderList = data;
            }else{
                this.orderList.push.apply(this.orderList, data) // 合并
            }
            this.$refs.myscroller.finishInfinite(true) // 加载完成

            this.page = curr_page // 当前页码数
            this.all_page = all_page // 总页码数
            done(); // 完成
        }
    }
}